<template>
    <modal :show.sync="createPortModal" effect="fade" width="550px">
        <div slot="modal-header" class="modal-header">
            <h4 class="modal-title"></h4>
        </div>
        <div slot="modal-body" class="modal-body">
            <form class="form-horizontal clearfix">
                <div class="form-group">
                    <label class="control-label col-sm-3"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-3">VLAN</label>
                    <div class="col-sm-8">
                        <v-select :value.sync="vlan" :options="vlans" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3"></label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-3">MAC</label>
                    <div class="col-sm-8">
                        <input type="text" class="form-control">
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-3"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="code" :options="codes" placeholder="">
                        </v-select>
                    </div>
                </div>
                <div class="form-group input-box">
                    <label class="control-label col-sm-3"></label>
                    <div class="col-sm-8">
                        <v-select :value.sync="port" :options="ports" placeholder="">
                        </v-select>
                    </div>
                </div>
            </form>
        </div>
        <div slot="modal-footer" class="modal-footer">
            <button type="button" class="btn btn-default"></button>
            <button type="button" class="btn btn-default" @click='createPortModal = false'></button>
        </div>
    </modal>
</template>

<script>
import { modal } from 'vue-strap'
import vSelect from '../../global/Select.vue'

let origin = {
        createPortModal: false,
        vlans: [],
        vlan: '',
        codes: [],
        code: '',
        ports: [],
        port: ''
    },
    init = Object.assign({}, origin);

export default {
    data () {
        return origin
    },
    methods: {

    },
    components: {
        modal,
        vSelect
    },
    events: {
        'showCreatPort' () {
            this.createPortModal = true
        }
    }
}
</script>

<style scoped>
</style>


