<template>
    <modal :show.sync="editDeviceModal" effect="fade" width="1200px">
        <div slot="modal-header" class="modal-header">
            <button type="button" class="close" @click="editDeviceModal = false">
                <span></span>
            </button>
            <h4 class="modal-title"></h4>
        </div>
        <div slot="modal-body" class="modal-body min-height">
            <tabs :active="0">
                <tab header="">
                    <form class="form-horizontal clearfix form-input">
                        <div class="col-sm-3">
                            <div class="form-group">
                                <label class="control-label col-sm-4">SN</label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group input-box">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <v-select :value.sync="room" :options="rooms" placeholder="">
                                    </v-select>
                                </div>
                            </div>
                            <div class="form-group input-box">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <v-select :value.sync="frame" :options="frames" placeholder="">
                                    </v-select>
                                </div>
                            </div>
                            <div class="form-group input-box">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <v-select :value.sync="seat" :options="seats" placeholder="">
                                    </v-select>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-4 input-box pr0">
                                    <v-select :value.sync="origin1" :options="origins1" placeholder="">
                                    </v-select>
                                </div>
                                <div class="col-sm-4 input-box pl0">
                                    <v-select :value.sync="origin2" :options="origins2" placeholder="">
                                    </v-select>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group input-box">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <v-select :value.sync="firm" :options="firms" placeholder="">
                                    </v-select>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-3">
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <datepicker
                                      :value.sync="addTime"
                                      :format="'yyyy-MM-dd'"
                                      :show-reset-button="true">
                                    </datepicker>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <datepicker
                                      :value.sync="factoryTime"
                                      :format="'yyyy-MM-dd'"
                                      :show-reset-button="true">
                                    </datepicker>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <datepicker
                                      :value.sync="procureTime"
                                      :format="'yyyy-MM-dd'"
                                      :show-reset-button="true">
                                    </datepicker>
                                </div>
                            </div>
                            <div class="form-group input-box">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <v-select :value.sync="status" :options="statusArr" placeholder="">
                                    </v-select>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-3">
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-4"></label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" placeholder="">
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-3">
                            <label class="control-label col-sm-4">IP</label>
                            <div class="col-sm-8">
                                <textarea rows="12" class="form-control"></textarea>
                            </div>
                        </div>
                    </form>
                    <div class="text-center mt30 mb20">
                        <button type="button" class="btn btn-default"></button>
                        <button type="button" class="btn btn-default" @click="$broadcast('showCreatVlan')">VLAN</button>
                        <button type="button" class="btn btn-default" @click="$broadcast('showCreatPort')"></button>
                        <button type="button" class="btn btn-default" @click='editDeviceModal = false'></button>
                    </div>
                </tab>
                <tab header="VLAN">
                    <table class="table table-hover">
                        <thead>
                            <tr>
                                <th>VLAN</th>
                                <th></th>
                                <th></th>
                                <th>IP</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="vlan in vlans">
                                <td v-text="vlan.name"></td>
                                <td v-text="vlan.speed"></td>
                                <td v-text="vlan.port" :title="vlan.port"></td>
                                <td v-text="vlan.ip"></td>
                                <td v-text="vlan.use"></td>
                                <td>
                                    <button class="btn btn-default btn-small">
                                        <span class="table-icon glyphicon glyphicon-trash"></span>
                                    </button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </tab>
                <tab header="">
                    <table class="table table-hover table-scroll">
                        <thead>
                            <tr>
                                <th></th>
                                <th>VLAN</th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th>VLANMAC</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="port in ports">
                                <td v-text="port.name"></td>
                                <td v-text="port.vlan"></td>
                                <td v-text="port.speed"></td>
                                <td v-text="port.status"></td>
                                <td v-text="port.device"></td>
                                <td v-text="port.devicePort"></td>
                                <td v-text="port.deviceVlan"></td>
                                <td v-text="port.use"></td>
                                <td class="pl20">
                                    <button class="btn btn-default btn-small">
                                        <span class="table-icon glyphicon glyphicon-trash"></span>
                                    </button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </tab>
            </tabs>
        </div>
        <div slot="modal-footer" class="modal-footer">
        </div>
    </modal>

    <create-vlan-modal></create-vlan-modal>
    <create-port-modal></create-port-modal>
</template>

<script>
import { modal, datepicker, tabset, tab } from 'vue-strap'
import vSelect from '../../global/Select.vue'
import createVlanModal from './CreateVlan.vue'
import createPortModal from './CreatePort.vue'

let origin = {
        editDeviceModal: false,
        vlans: [{name: 'vlan1', speed: '1Gbps', port: 'G23,G07,G15,G18,G02,G10,G2', ip: '', use: ''}],
        ports: [
            {name: 'G01', vlan: 'vlan1', speed: '1Gbps', status: '', device: '', devicePort: '', deviceVlan: '', use: ''},
            {name: 'G01', vlan: 'vlan1', speed: '1Gbps', status: '', device: '', devicePort: '', deviceVlan: '', use: ''},
            {name: 'G01', vlan: 'vlan1', speed: '1Gbps', status: '', device: '', devicePort: '', deviceVlan: '', use: ''},
            {name: 'G01', vlan: 'vlan1', speed: '1Gbps', status: '', device: '', devicePort: '', deviceVlan: '', use: ''},
            {name: 'G01', vlan: 'vlan1', speed: '1Gbps', status: '', device: '', devicePort: '', deviceVlan: '', use: ''},
            {name: 'G01', vlan: 'vlan1', speed: '1Gbps', status: '', device: '', devicePort: '', deviceVlan: '', use: ''}
        ],
        firms: [],
        firm: '',
        statusArr: [],
        status: '',
        addTime: '',
        factoryTime: '',
        procureTime: '',
        origins1: [],
        origin1: '',
        origins2: [],
        origin2: '',
        rooms: [],
        room: '',
        frames: [],
        frame: '',
        seats: [],
        seat :''

    },
    init = Object.assign({}, origin);

export default {
    data () {
        return origin
    },
    methods: {

    },
    components: {
        modal,
        vSelect,
        createVlanModal,
        createPortModal,
        datepicker,
        tabs: tabset,
        tab
    },
    events: {
        'showEditDevice' () {
            this.editDeviceModal = true
        }
    }
}
</script>

<style scoped>
.min-height {
    min-height: 500px;
}
</style>


